<template>
    <div >
        <div style="margin:22px 30px; ">
            <el-steps :space="200" :active="4" finish-status="success">
                <el-step title="下单"></el-step>
                <el-step title="付款"></el-step>
                <el-step title="派送"></el-step>
                <el-step title="收货评价"></el-step>
            </el-steps>
        </div>
        <div class="shaddr-wrap">
            <div class="logo"><i class="fa fa-location-arrow"></i></div>
            <div v-if="receaddress!=null" class="bd">

                <div class="contact">
                    {{receaddress.mobile}}
                    {{receaddress.shr}}
                </div>
                <div class="addinfo">{{receaddress.addinfo}}
                    <span>{{receaddress.postcode}}</span>
                </div>

            </div>

        </div>


        <table v-for="od in order.orderDetail" class="smart-table" border="1">
            <tr>
                <td class="tlabel">订单编号</td>
                <td>{{order.ddno}}</td>
                <td class="tlabel">下单时间:</td>
                <td>
                    {{order.createtime}}

                </td>
            </tr>
            <tr>
                <td class="tlabel">商品</td>
                <td colspan="3">
                    <img width="80" height="80" :src="hostHead+od.images[0]"/>
                    {{od.spname}}
                    {{od.price}}¥X{{od.count}}
                </td>
            </tr>
            <tr>
                <td class="tlabel">评价:</td>
                <td colspan="3">
                    <el-rate v-model="star"></el-rate>
                </td>
            </tr>
            <tr>
                <td class="tlabel">描述</td>
                <td colspan="3">
                    <div v-if="od.state==3" style="width: 300px;" >
                        <el-input
                            type="textarea"
                            :rows="2"
                            placeholder="请输入内容"
                            v-model="des"
                            clearable>
                        </el-input>
                    </div>
                    <div v-if="od.state==4">
                        {{od.comment.des}}
                    </div>
                </td>
            </tr>
            <tr v-if="od.state==3">

                <td colspan="4">
                    <el-button @click="pingjiaHandler" type="danger"><i class="el-icon-circle-plus"></i>收货评价</el-button>
                </td>
            </tr>

        </table>




    </div>

</template>

<script>
    export default {
        name: "shouhuo",
        data() {
            return {
                paypwd: '',
                hostHead: this.$app.contextPath,
                order: {},
                star: 0,
                des: '',
                receaddress: {},
                huiyuan: null
            }
        },
        props: {
            params:{
                type:Object,
                default:{}
            },
        },
        methods: {

            async getOrderInfo() {
                let {data: res} = await this.$http.post("admin/shorder/load", {id: this.params.id});
                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                res.data.orderDetail = res.data.orderDetail.filter(c => {
                    return c.id == this.params.oiid;
                });
                console.log("订单数据", res);
                this.order = res.data;

            },
            async getHuiyuanInfo() {

                let {data: res} = await this.$http.post("admin/huiyuan/load", {id: this.order.purchaser});

                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.huiyuan = res.data;
                console.log("huiyuan", this.huiyuan);
            },
            async getAddress() {

                let {data: res} = await this.$http.post("admin/receaddress/load", {id: this.order.addid});

                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.receaddress = res.data;
                console.log("receaddress", this.receaddress);
            },
            async pingjiaHandler() {
                if (this.star == 0) {
                    this.$message.error("请选择评价等级");
                    return;
                }
                if (this.des == "") {
                    this.$message.error("请填写评论");
                    return;
                }
                let {data: res} = await this.$http.post("admin/shorder/shouhuo", {
                    oiid: this.params.oiid,
                    star: this.star,
                    des: this.des,
                    appraiserid: this.huiyuan.id
                });

                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.$message.success("评论完成");
                this.$emit("close",false);


            }

        },
        async created() {
            await this.getOrderInfo();
            await this.getHuiyuanInfo();
            await this.getAddress();
        }

    }
</script>

